import { Component, ChangeDetectorRef, ViewChild } from '@angular/core';
import { IonicPage,Content, NavController, NavParams, AlertController, Platform } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { DomSanitizer } from '@angular/platform-browser';
import { ToolsProvider } from '../../providers/tools/tools'
import { Keyboard } from '@ionic-native/keyboard';
@IonicPage()
@Component({
  selector: 'page-forgot-id',
  templateUrl: 'forgot-id.html',
  providers: [Keyboard]
})
export class ForgotIdPage {
  @ViewChild(Content) content: Content;
  forgotResevationIdForm: FormGroup;
  isChecked = false;
  isCheckedPrivacy = false;
  htmlData: any = null; // to manage html content
  res2: any; //to store return data while language translate
  isKeyboardOpen:boolean=false;
  devicePlatForm: any = "android";
  htmlDataPrivacy: any;

  constructor(private keyboard: Keyboard,public navCtrl: NavController, public navParams: NavParams, public formBuilder: FormBuilder, public sanitized: DomSanitizer,
    public alertCtrl: AlertController, public iab: InAppBrowser, public tools: ToolsProvider, public cdRef: ChangeDetectorRef, private platForm: Platform) {

    this.forgotResevationIdForm = formBuilder.group({
      'name': new FormControl(''),
      'surName': new FormControl(''),
      'email': new FormControl('', [Validators.required, Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)]),
      'locator': new FormControl('')
    });
    window.addEventListener('keyboardWillHide', (event) => {
      this.isKeyboardOpen=false;
      this.content.scrollToBottom();
    });

    if (this.platForm.is("ios")) {
      this.devicePlatForm = "ios";
    }

  }
  fnKeyboardOpen(){
    this.isKeyboardOpen=true;
  }

  ionViewDidLoad() {
    let current = this;

    this.res2 = this.tools.fnLanguageTranslate("view.login.label.privacy");
    this.res2.subscribe((value: string) => {
      this.htmlDataPrivacy = this.sanitized.bypassSecurityTrustHtml(value);
      this.cdRef.detectChanges();

      /* click binding to termsofservice span element and redirection */
      let termsElmnt1 = document.querySelector('ion-nav').querySelectorAll("#privacyPolicy");
      for (let i = 0; i < termsElmnt1.length; i++) {
        if (termsElmnt1[i]) {
          termsElmnt1[i].addEventListener('click', function (e) {
            current.iab.create(current.tools.getPrivacyUrl(), "_blank", "location=yes,toolbar=yes,hardwareback=yes");
          });
        }
      }
      /* click binding to termsofservice span element and redirection */
      let termsElmnt = document.querySelector('ion-nav').querySelectorAll("#termsOfService");
      for (let i = 0; i < termsElmnt.length; i++) {
        if (termsElmnt[i]) {
          termsElmnt[i].addEventListener('click', function (e) {
            current.iab.create(current.tools.getLegalUrl(), "_blank", "location=yes,toolbar=yes,hardwareback=yes");
          });
        }
      }

    });
  }

  ionViewDidEnter() {
    this.keyboard.disableScroll(true);

  }

  /**
  * Function to go back to previous page
  */
  public fnGoBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  /**
   * function to select or deselect the radio button
   * @param  event
   */
  public fnOnRadioSelect($event) {
    this.isChecked = !this.isChecked;
  }

  public fnOnRadioSelectPrivacy($event) {
    this.isCheckedPrivacy = !this.isCheckedPrivacy;
  }

  /**
   * function to valdate and execute after submitting the form
   */
  public fnSubmit() {

    if (!this.forgotResevationIdForm.controls['email'].valid && !this.isCheckedPrivacy) {
      // if email is empty show error
      let msg = this.tools.fnLanguageTranslate(["view.login.error.emptyMail", "common.error", "buttons.ok"]); // translate of html content with common funtion
      msg.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.error'],
          message: value['view.login.error.emptyMail'],
          cssClass:'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);

      });
    } else {
      // common error message - service unavailable
      let errmsg = this.tools.fnLanguageTranslate(["common.serviceIsUnavailable", "common.error", "buttons.ok"]); // translate of html content with common funtion
      errmsg.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title:  value['common.error'],
          message:  value['common.serviceIsUnavailable'],
          cssClass:'cls_alrt',
          buttons: [
            {
              text:  value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);

      });
    }
  }


}



